export interface UserInfo {
    orgAllPath: string;
    abbreviation: string;
    code: string;
    defaultOrgId: string;
    defaultOrgPath: string;
    ext1: string;
    ext2: string;
    fullPathName: string;
    name: string;
    orgTypeId: string;
    secLevel: string;
    sortOrder: string;
    state: string;
    su: string;
    userId: string;
}

export interface PersonnelRequestParam {
    pageSize?: number;
    pageIndex?: number;
    withAllOrg?: number; // 获取组织及其所有下级组织下的用户
    notGetStopUser?: boolean; // 是否获取停用用户，默认是true
    notGetCurrentUser?: boolean; // 是否获取当前用户，默认是true
    curUser?: boolean; // 获取当前用户信息，默认是false
    getUserInfo?: boolean; // 是否获取用户头像，默认是false
    orgId?: string;
    posId?: string;
    orderField?: string; // 排序方式
    userTypeMode?: string; // 工作流用
    searchMode?: string; // 搜索模式，一般为or
    pinyin?: string; // 全拼搜索
    initials?: string; // 首字母搜索
    filterId?: string;
    groupId?: string; // 用户分组id
    withAllGroup?: boolean; // 是否获取所有下级用户分组内的用户
    withPermission?: boolean;
    authOp?: string;
}

export interface RfRequestParam {
    absOrgType: string;
    bizId: string;
    objectValue: string;
    addList?: string[];
    deleteList?: string[];
    notGetStopUser?: boolean;
}

export interface OrgRequestParam {
    absOrgType: string; // 组织类型，默认为系统组织
    layer?: number; // 获取第几层的组织
    codeOrName?: string; // 根据编号和名称模糊搜索
    name?: string; // 根据名称模糊搜索
    parentID?: string; // 根据父节点id获取下级组织
    isSearchTree?: boolean; // 搜索时，是否返回树结构，默认false
    getCurUser?: boolean;   // 是否获取当前用户，默认true
    notGetStopOrg?: boolean; // 不获取停用组织，默认true
    id?: string; // 组织id页,
    getAllParentOrg?: boolean; // 是否获取全部上级组织，默认true
    getOrgAllPath?: boolean; // 获取组织全路径
    getOrgAllPathInfo?: boolean;
}

export interface PanelMode {
    showQuickMode: boolean;
    showAdvancedMode: boolean;
}

export class FilterPosition {
    id: string;
    code: string;
    name: string;
}

export class FilterOrg {
    id: string;
    value: string;
    name: string;
}