import {
    Component, Input, EventEmitter, Output, ViewChild, OnInit, AfterViewInit, TemplateRef,
    SimpleChanges, OnChanges, Optional, ChangeDetectorRef
} from '@angular/core';
import { ApiLocalService } from '../../utils/api-service';
import { DatagridComponent } from '@farris/ui-datagrid';
import * as _ from 'lodash-es';
import { FavoritesTool } from '../../utils/favorites.tool';
import { noDataImg } from '../../assets/resource';
import { DataLoadingService } from '../../utils/loading-service';
import { FilterPosition } from '../../utils/user.type';
import { switchMap, tap } from 'rxjs/operators';

@Component({
    selector: 'lib-unselect',
    templateUrl: './unselectdatagrid.component.html',
    styleUrls: ['./unselectdatagrid.component.css']
})
export class UnSelectDatagridComponent implements OnInit, AfterViewInit, OnChanges {

    @ViewChild('unSelectDatagrid') unSelectDatagrid: DatagridComponent;
    @ViewChild('opCell') opCell: TemplateRef<any>;

    @Input() idField: string = 'userId';
    @Input() items = [];
    @Input() absUserOrgUrl: any;
    @Input() absOrgType: any;
    @Input() pageIndex: any;
    @Input() pageSize: any;
    @Input() pageTotalAll: any;
    @Input() multiSelect: any;
    @Input() noSelectToRight: any;
    @Input() searchText: any;
    @Input() unSelectedList: any;
    @Input() pageTotal: any;
    @Input() set enableChildHierarchy(value: boolean) {
        this.pEnableChildHierarchy = value;
    }
    @Input() orgId = '';
    @Input() userIds = '';
    @Input() unSelectedUserIds = '';
    @Input() includeStopUser: boolean;
    @Input() searchPage: boolean;
    @Input() set includeCurrentUser(value: boolean) {
        this.pIncludeCurrentUser = value;
    }
    @Input() enablePagination = true;
    @Input() enableFavorites: boolean;
    @Input() busNum: string;
    @Input() orderField: string;
    @Input() userTypeMode: string;
    @Input() globalSearch: boolean;
    @Input() showPageList: boolean;
    @Input() dataLoadingService: DataLoadingService;
    @Input() validElementsInfo: string;
    @Input() filterPositionErrorInfo: string;
    @Input() withPermission: boolean;
    @Output() selectionsChange = new EventEmitter<any>();
    @Output() searchResultChange = new EventEmitter<boolean>();

    columns: any;
    selections: any[] = [];

    cloneUserIds = '';
    cloneSelections = [];

    private pEnableChildHierarchy = true;
    private pIncludeCurrentUser = true;
    favoritesList = [];
    noDataImg = noDataImg;
    allChecked = false;
    itemsBackup = [];
    allIndexId = 'allIndexId';
    filterId: string;
    // 是否显示用户编号
    showUserCode = true;
    cdRef: ChangeDetectorRef;
    showPagesPanel: boolean = false;
    pageList = [10, 20, 50, 100, 200, 500, 1000, 2000, 5000];
    loadingServiceMessage: string;
    filterPersonWithPositionUrl: string = '/api/runtime/sys/v1.0/users/getPositionsByFilter';
    filterPositionArr: Array<FilterPosition> = [];
    filterPositionId: string = null;
    isEmptyFilterPosition: boolean = false;

    constructor(
        private apiService: ApiLocalService,
        @Optional() private favoritesTool: FavoritesTool
    ) {
        this.allIndexId = Math.random().toString(16).substring(3, 10);
    }

    ngOnInit(): void {
        let title = '';
        if (this.multiSelect) {
            title = '全选';
        }
        this.columns = [
            {
                field: 'name', template: this.opCell, title, width: '150'
            }
        ];
        if (this.favoritesTool) {
            this.favoritesTool.favoritesSubject.subscribe(data => {
                this.favoritesList = data;
                this.unSelectedListLoad();
            });
        }
    }

    ngAfterViewInit(): void {
        // this.pageChangeCheck();
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.includeCurrentUser && typeof changes.includeCurrentUser.currentValue === 'boolean'
            && !changes.includeCurrentUser.firstChange) {
            this.assembleDataByOrgId();
        }
        if (changes.enableChildHierarchy && typeof changes.enableChildHierarchy.currentValue === 'boolean'
            && !changes.enableChildHierarchy.firstChange) {
            this.assembleDataByOrgId();
        }
    }

    private emitSelections() {
        const emitObj = {
            selections: this.selections,
            token: 'unDatagrid'
        };
        this.selectionsChange.emit(emitObj);
    }

    restoreSelections() {
        this.selections = _.cloneDeep(this.cloneSelections);
        // this.emitSelections();
        this.toggleChecked();
    }

    // 人员展示翻页触发查询（！）
    switchPage(size) {
        this.pageIndex = size.pageIndex;
        this.pageSize = size.pageSize;
        if (this.searchPage) {
            this.assembleDataBySearch();
        } else {
            this.assembleDataByOrgId();
        }
    }

    toggleFavorites(e: any, item: any) {
        this.favoritesTool.toggleFavorites(e, item);
    }

    assembleDataBySearch() {
        const requestParam: any = {
            pageSize: this.pageSize,
            pageIndex: this.pageIndex,
            withAllOrg: this.pEnableChildHierarchy ? 1 : 0,
            notGetStopUser: !this.includeStopUser,
            notGetCurrentUser: !this.pIncludeCurrentUser,
            orderField: this.orderField,
            userTypeMode: this.userTypeMode,
            filterId: this.filterId
        };
        // 全局搜索or局部
        if (!this.globalSearch) {
            requestParam.orgId = this.orgId;
        }
        this.apiService.getPersonnelBySearch(this.absUserOrgUrl, this.absOrgType, this.searchText,
            requestParam).subscribe((res: any) => {
                const rcResData = res ? res.data : [];
                this.unSelectedList = rcResData;
                this.pageTotalAll = res.totalCount;
                this.pageTotal = Math.ceil(this.pageTotalAll / this.pageSize);
                if (this.pageTotal === 0) {
                    this.pageTotal = 1;
                }
                this.unSelectedListLoad();
                this.searchResultChange.emit(true);
            });
    }

    assembleDataByOrgId() {
        let orgIds = '';
        if (!this.orgId) {
            return;
        }
        if(this.dataLoadingService) {
            this.dataLoadingService.show(this.loadingServiceMessage);
        }
        orgIds = this.orgId === '*' ? '' : this.orgId;
        const requestParam: any = {
            withAllOrg: this.pEnableChildHierarchy ? 1 : 0,
            notGetStopUser: !this.includeStopUser,
            notGetCurrentUser: !this.pIncludeCurrentUser,
            orderField: this.orderField,
            userTypeMode: this.userTypeMode,
            filterId: this.filterId
        };
        if (this.enablePagination) {
            requestParam.pageSize = this.pageSize;
            requestParam.pageIndex = this.pageIndex;
        }
        // 通过岗位进行人员的二次过滤
        if(this.filterPositionId) {
            requestParam.posId = this.filterPositionId;
        }
        if(this.withPermission) {
            requestParam.withPermission = true;
            requestParam.authOp = 'UserManage';
        }
        // 根据组织加载人员
        this.apiService.getPersonnelByOrgId(this.absUserOrgUrl, this.absOrgType, orgIds, requestParam).subscribe((res: any) => {
            const rcResData = res ? res.data : [];
            this.unSelectedList = rcResData;
            this.pageTotalAll = res.totalCount;
            this.pageTotal = Math.ceil(this.pageTotalAll / this.pageSize);
            if (this.pageTotal === 0) {
                this.pageTotal = 1;
            }
            this.unSelectedListLoad();
            if(this.dataLoadingService) {
                this.dataLoadingService.hide();
            }
        });
    }

    assembleDataByOrgIdAndGetFilterPosition(size: any) {
        if(!this.orgId || this.orgId == '*') {
            return;
        }
        if(this.dataLoadingService) {
            this.dataLoadingService.show(this.loadingServiceMessage);
        }
        this.pageIndex = size.pageIndex;
        this.pageSize = size.pageSize;
        this.filterPositionArr = [];
        this.filterPositionId = null;

        const filterRequestParam: any = {
            withAllOrg: true,
            getUserInfo: false,
            notGetStopUser: !this.includeStopUser,
            notGetCurrentUser: !this.pIncludeCurrentUser,
            orderField: this.orderField,
            userTypeMode: this.userTypeMode,
            filterId: this.filterId,
            orgId: this.orgId,
        };
        // 首先获取当前组织下所有成员所属的岗位
        const getFilterPosition$ = this.apiService.getFilterPosition(this.filterPersonWithPositionUrl, this.absOrgType, filterRequestParam);

        const requestParam: any = {
            withAllOrg: this.pEnableChildHierarchy ? 1 : 0,
            notGetStopUser: !this.includeStopUser,
            notGetCurrentUser: !this.pIncludeCurrentUser,
            orderField: this.orderField,
            userTypeMode: this.userTypeMode,
            filterId: this.filterId
        };
        if (this.enablePagination) {
            requestParam.pageSize = this.pageSize;
            requestParam.pageIndex = this.pageIndex;
        }
        if(this.withPermission) {
            requestParam.withPermission = true;
            requestParam.authOp = 'UserManage';
        }
        // 然后获取当前组织下的所有成员
        const getPersonnel$ = this.apiService.getPersonnelByOrgId(this.absUserOrgUrl, this.absOrgType, this.orgId, requestParam);
        getFilterPosition$.pipe(
            switchMap(
                (resData: any) => {
                    if(resData && resData.length > 0) {
                        this.isEmptyFilterPosition = false;
                        let filterPosition: FilterPosition;
                        this.filterPositionArr = (resData as Array<any>).map((data: any) => {
                            filterPosition = new FilterPosition;
                            filterPosition.id = data['id'];
                            filterPosition.code = data['code'];
                            filterPosition.name = data['name'];
                            return filterPosition;
                        });
                    } else {
                        this.isEmptyFilterPosition = true;
                    }
                    return getPersonnel$.pipe(
                        tap(
                            (res: any) => {
                                const rcResData = res ? res.data : [];
                                this.unSelectedList = rcResData;
                                this.pageTotalAll = res.totalCount;
                                this.pageTotal = Math.ceil(this.pageTotalAll / this.pageSize);
                                if (this.pageTotal === 0) {
                                    this.pageTotal = 1;
                                }
                                this.unSelectedListLoad();
                                if(this.dataLoadingService) {
                                    this.dataLoadingService.hide();
                                }
                            },
                            (error) => {
                                if(this.dataLoadingService) {
                                    this.dataLoadingService.hide();
                                }
                                throw new Error(this.filterPositionErrorInfo);
                            }
                        )
                    );
                } 
            )
        ).subscribe();
    }

    /**
     * datagrid用于数据展示
     */
    unSelectedListLoad(initLoad = false) {
        this.items = [];
        this.unSelectedList.forEach(res => {
            // const indexId = Math.random().toString(16).substring(2);
            // const item = {
            //     abbreviation: res.abbreviation,
            //     code: res.code,
            //     defaultOrgId: res.defaultOrgId,
            //     defaultOrgPath: res.defaultOrgPath,
            //     ext1: res.ext1,
            //     ext2: res.ext2,
            //     fullPathName: res.fullPathName,
            //     name: res.name,
            //     orgTypeId: res.orgTypeId,
            //     secLevel: res.secLevel,
            //     sortOrder: res.sortOrder,
            //     state: res.state,
            //     su: res.su,
            //     userId: res.userId,
            //     orgAllPath: res.orgAllPath,
            //     inFavorites: false,
            //     type: 'people',
            //     checked: false,
            //     indexId
            // };
            const item = _.cloneDeep(res);
            item.checked = false;
            item.inFavorites = false;
            item.indexId = Math.random().toString(16).substring(2);
            if (this._isInArray(res[this.idField], this.idField, this.selections) !== -1) {
                item.checked = true;
            }
            if (this._isInArray(res[this.idField], this.idField, this.favoritesList) !== -1) {
                item.inFavorites = true;
            }
            item.displayName = !!this.showUserCode ? ('[' + item.code + ']' + item.name) : item.name;
            this.items.push(item);
            this.itemsBackup.push(item);
        });
        this.toggleChecked();
        this.items = this.items.slice();
        this.cdRef ? this.cdRef.detectChanges() : null;
    }

    toggleAll() {
        if (!this.allChecked) {
            this.checkAll();
        } else {
            this.unCheckAll();
        }
        this.emitSelections();
    }

    toggleOne(data) {
        if (data.checked) {
            this.unSelectLine(data);
        } else {
            this.selectLine(data);
        }
        this.emitSelections();
    }

    /**
     * 全选后处理
     * @param dataArr 全选的值
     */
    checkAll() {
        this.items.forEach(item => {
            this._insertOneIntoSelections(item);
            item.checked = true;
        });
    }

    /**
     * 全部取消
     * @param dataArr 取消的数据
     * @returns void
     */
    unCheckAll() {
        this.items.forEach(item => {
            this.selections.forEach((selection, index) => {
                if (item[this.idField] === selection[this.idField]) {
                    this.selections.splice(index, 1);
                }
            });
            item.checked = false;
        });
    }

    /**
    * 当前行选中
    * @param data 当前行的数据（）
    */
    selectLine(data: any) {
        if (this.multiSelect) {
            this._insertOneIntoSelections(data);
        } else {
            this.userIds = '';
            this.selections = [];
            this.items.forEach(item => {
                if (item.checked) {
                    item.checked = false;
                }
            });
            this._insertOneIntoSelections(data);
        }
        if (this.isAllChecked()) {
            this.allChecked = true;
        }
    }

    /**
     * 单选取消选中
     * @param data 取消选中行
     */
    unSelectLine(data: any) {
        const index = this._isInArray(data[this.idField], this.idField, this.selections);
        if (index > -1) {
            this.selections.splice(index, 1);
        }
        if (this.allChecked) {
            this.allChecked = false;
        }
    }

    /**
     *  取消选中
     * @param $event 取消勾选的数据
     */
    cancelSelections($event) {
        if (this.items.findIndex(item => item[this.idField] === $event.id) === -1) {
            return;
        }
        this.items.forEach(item => {
            if (item[this.idField] === $event.id) {
                item.checked = false;
            }
        });
        this.selections.forEach((selection, index) => {
            if ($event.id === selection[this.idField]) {
                this.selections.splice(index, 1);
            }
        });
        this.allChecked = false;
    }

    /**
     * 全部取消勾选
     */
    clearCheckeds() {
        this.items.forEach(item => {
            item.checked = false;
        });
        this.allChecked = false;
        this.selections = [];
    }

    clearUserIds() {
        this.userIds = '';
    }
    /**
     * 选中并插入当前行数据到右侧
     * @param data 当前行的数据
     * @param collection 暂存当前数据的集合
     */
    _insertOneIntoSelections(data: any) {
        // 如果当前数据已经移入右侧数据，则返回。
        if (this._isInArray(data[this.idField], this.idField, this.selections) > -1) {
            return;
        }
        this.selections.push(data);
        this.selections = this.selections.slice();
    }

    /**
     * 判断是否在内
     * @param value value
     * @param field field
     * @param array array
     * @returns 位置索引
     */
    _isInArray(value: string, field: string, array: any[]): number {
        if (!field || !value) {
            throw new Error(this.validElementsInfo);
        }
        return array.findIndex(item => value === item[field]);
    }

    /**
     * 翻页检查：人员展示区的勾选状态与已选人员区是否对应
     */
    toggleChecked() {
        this.items.forEach(item => {
            if (this._isInArray(item[this.idField], this.idField, this.selections) > -1) {
                item.checked = true;
            } else {
                item.checked = false;
            }
        });
        if (this.isAllChecked()) {
            this.allChecked = true;
        } else {
            this.allChecked = false;
        }
    }

    private isAllChecked(): boolean {
        let count = 0;
        let allChecked = false;

        this.items.forEach(item => {
            this.selections.forEach(selection => {
                if (item[this.idField] === selection[this.idField]) {
                    count += 1;
                }
            });
        });
        if (this.items.length === count && count > 0) {
            allChecked = true;
        }
        return allChecked;
    }

    trackByFn(index, item) {
        return item.userId;
    }

    turnOverPage(dir: string, num: number) {
        if ((dir === 'pre' && this.pageIndex === 1) || (dir === 'next' && this.pageIndex === this.pageTotal)) {
            return;
        }
        this.pageIndex = this.pageIndex + num;
        this.switchPage({
            pageIndex: this.pageIndex,
            pageSize: this.pageSize,
        });
    }

    beforePageIndexChange(inputEl: KeyboardEvent, maxNumber) {
        const target = inputEl.target as any;
        const currentPageNumber = Number(target.value);
        if (currentPageNumber > maxNumber) {
            target.value = maxNumber;
        } else {
            if (currentPageNumber < 1) {
                target.value = 1;
            }
        }
        this.pageIndex = Number(target.value);
    }

    onMouseEnter($event){
        this.showPagesPanel = true;
    }

    onMouseLeave($event) {
        this.showPagesPanel = false;
    }

    changePageSizeHandler($event, num){
        $event.stopPropagation();
        if(num == this.pageSize) {
            return;
        }
        this.showPagesPanel = false;
        this.pageSize = num;
        this.switchPage({
            pageIndex: 1,
            pageSize: this.pageSize,
        });
    }
}
