import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';
import { OrgRequestParam, PersonnelRequestParam } from './user.type';

@Injectable()
export class ApiLocalService {

    constructor(public http: HttpClient) { }

    filterGroupLeader(param: any) {
        const url = '/api/runtime/sys/v1.0/absRecentContacts/modify';
        const options = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
            }),
        };
        return this.http.post(url, param, options);
    }

    getGroupLeaderStatus(param: any) {
        const url = '/api/runtime/sys/v1.0/absRecentContacts/getRecentContacts';
        const options = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
            }),
        };
        return this.http.post(url, param, options);
    }

    /**
     * 获取行政组织
     * @returns observable
     */
    getOrganization(url: string, param: OrgRequestParam) {
        const options = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
            }),
        };
        return this.http.post(url + '/org', param, options);
    }

    /**
     * 获取系统用户分组
     * @returns observable
     */
     getSysUserGroup(param: any) {
        const url = '/api/runtime/sys/v1.0/usergroups';
        const paramStr = JSON.stringify(param);
        const options = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
            }),
            params: {
                param: paramStr,
            }
        };
        return this.http.get(url, options);
    }

    /**
     *
     * @param url url地址
     * @param absOrgType 系统组织
     * @param orgId 组织id
     * @param requestParam 请求参数
     * @returns observable
     */
    getPersonnelByOrgId(url: string, absOrgType: string, orgId: string, requestParam: PersonnelRequestParam) {
        const body = {
            absOrgType, // 组织类型，默认为系统组织
            pageIndex: requestParam.pageIndex, // 当前页
            pageSize: requestParam.pageSize, // 每页记录数
            getUserInfo: true, // 是否获取用户头像
            orgId, // 组织id，获取当前组织下的用户
            withAllOrg: requestParam.withAllOrg, // 获取组织及其所有下级组织下的用户，默认false
            notGetStopUser: requestParam.notGetStopUser, // 是否获取停用用户，默认为false，
            notGetCurrentUser: requestParam.notGetCurrentUser, // 是否获取当前用户，默认为true，
            orderField: requestParam.orderField, // 排序
            userTypeMode: requestParam.userTypeMode,
            filterId: requestParam.filterId,
            posId: requestParam.posId,
            withPermission: requestParam.withPermission,
            authOp: requestParam.authOp
        };
        const options = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
            })
        };
        return this.http.post(url + '/user', body, options);
    }

    // 根据系统用户分组id获取分组下成员
    getPersonnelByUserGroupId(url: string, requestParam: PersonnelRequestParam) {
        const body = {
            pageIndex: requestParam.pageIndex, // 当前页
            pageSize: requestParam.pageSize, // 每页记录数
            getUserInfo: true, // 是否获取用户头像
            withAllGroup: requestParam.withAllGroup,
            notGetStopUser: requestParam.notGetStopUser, // 是否获取停用用户，默认为false，
            notGetCurrentUser: requestParam.notGetCurrentUser, // 是否获取当前用户，默认为true，
            orderField: requestParam.orderField, // 排序
            userTypeMode: requestParam.userTypeMode,
            filterId: requestParam.filterId,
            groupId: requestParam.groupId,
        };
        // 需要根据组织进行人员二次过滤
        if(requestParam.orgId) {
            body['orgId'] = requestParam.orgId;
        }
        const options = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
            })
        };
        return this.http.post(url + '/user', body, options);
    }

    /**
     * 查询行政人员（根据人员id返回相关数据）
     * @param url url地址
     * @param absOrgType 系统类型
     * @param ids 人员id
     * @returns observable
     */
    getPersonnelByUserIds(url: string, absOrgType: string, ids: string) {
        let idsArr = [];
        if (!!ids) {
            const lastChar = ids.substring(ids.length - 1);
            ids = lastChar === ',' ? ids.substring(0, ids.length - 1) : ids;
            idsArr = ids.split(',');
        }
        const urls = url + '/user/' + absOrgType;
        const options = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
            })
        };
        return this.http.post(urls, idsArr, options);
    }

    /**
     * 搜索行政人员（根据名称、编号搜索）
     * @param url 传入的url
     * @param absOrgType 数据源类型
     * @param codeOrName 传入的搜索内容
     * @param requestParam 请求参数
     * @returns observable
     */
    getPersonnelBySearch(url: string, absOrgType: string, codeOrName: string, requestParam: PersonnelRequestParam) {
        const body: any = {
            absOrgType, // 组织类型，默认为系统组织
            getUserInfo: true, // 是否获取用户头像,
            codeOrName, // 搜索内容
            withAllOrg: requestParam.withAllOrg, // 获取组织及其所有下级组织下的用户，默认false
            notGetStopUser: requestParam.notGetStopUser, // 是否获取停用用户，默认为false，
            notGetCurrentUser: requestParam.notGetCurrentUser, // 是否获取当前用户，默认为true，
            orderField: requestParam.orderField, // 排序
            userTypeMode: requestParam.userTypeMode,
            searchMode: 'or',
            pinyin: codeOrName,
            initials: codeOrName,
            filterId: requestParam.filterId
        };
        if (requestParam.orgId) {
            body.orgId = requestParam.orgId;
        }
        if (requestParam.pageSize) {
            body.pageSize = requestParam.pageSize;
        }
        if (requestParam.pageIndex) {
            body.pageIndex = requestParam.pageIndex;
        }
        const options = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
            })
        };
        return this.http.post(url + '/user', body, options);
    }




    /**
     * 获取群组信息
     * @param url 接口地址
     * @returns observable
     */
    getGroupOrGroupPersonnel(url: string, param?: { isenable: boolean, parentid: string }) {
        let baseUrl = url;
        if (param.parentid) {
            baseUrl += '?iseanble=' + param.isenable + '&parentid=' + param.parentid;
        }
        const options = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
            })
        };
        return this.http.get(baseUrl, options);
    }

    // 获取选人组件全局配置参数
    getGlobalParamConfig(url: string) {
        const options = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
            })
        };
        return this.http.post(url, options);
    }

    // 获取组织架构下所有成员所属的岗位，用于人员二次过滤
    getFilterPosition(url: string, absOrgType: string, requestParam: PersonnelRequestParam) {
        const body = {
            absOrgType,
            withAllOrg: requestParam.withAllOrg,
            getUserInfo: requestParam.getUserInfo,
            notGetStopUser: requestParam.notGetStopUser,
            notGetCurrentUser: requestParam.notGetCurrentUser,
            orderField: requestParam.orderField,
            userTypeMode: requestParam.userTypeMode,
            filterId: requestParam.filterId,
            curUser: false,
            orgId: requestParam.orgId,
        };
        const options = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
            })
        };
        return this.http.post(url, body, options);
    }

    // 获取用户分组下所有成员所属的组织，用于人员二次过滤
    getFilterOrganization(url: string, absOrgType: string, requestParam: PersonnelRequestParam) {
        const body = {
            absOrgType,
            withAllOrg: requestParam.withAllOrg,
            getUserInfo: requestParam.getUserInfo,
            notGetStopUser: requestParam.notGetStopUser,
            notGetCurrentUser: requestParam.notGetCurrentUser,
            orderField: requestParam.orderField,
            userTypeMode: requestParam.userTypeMode,
            filterId: requestParam.filterId,
            curUser: false,
            groupId: requestParam.groupId,
        };
        const options = {
            headers: new HttpHeaders({
                'Content-Type': 'application/json',
            })
        };
        return this.http.post(url, body, options);
    }
}
