import {
    Component, Input, EventEmitter, Output, OnInit, AfterViewInit, ChangeDetectorRef, Optional, Injector
} from '@angular/core';
import * as _ from 'lodash-es';
import { noDataImg } from '../../assets/resource';
import { NotifyService } from '@farris/ui-notify';
import { FavoritesTool } from '../../utils/favorites.tool';

@Component({
    selector: 'lib-search-result',
    templateUrl: './srp.component.html',
    styleUrls: ['./srp.component.css'],
})
export class SearchResultComponent implements OnInit, AfterViewInit {

    @Input() idField: string;
    @Input() multiSelect: boolean;
    @Input() enableFavorites: boolean;
    @Input() set data(value: any[]) {
        this.items = value;
        this.loadSearchResult();
    }
    @Input() validElementsInfo: string;
    @Output() selectionsChange = new EventEmitter<any>();
    @Output() closePanelChange = new EventEmitter<boolean>();
    @Output() pageIndexChange = new EventEmitter<any>(); 

    private cdRef: ChangeDetectorRef;
    // 当前搜索页中所有数据已被勾选
    allChecked = false;
    columns: any;
    // 右侧已选人员数据
    selections: any[] = [];

    cloneSelections = [];
    allIndexId = 'allIndexId';
    noDataImg = noDataImg;
    // 搜索结果，用于页面展示
    items = [];
    recentContactsList = [];
    favoritesList = [];
    // 搜索结果页索引
    pageIndex = 1;
    // 搜索结果总页数
    pageTotal: number;
    // 搜索结果总数量
    itemTotalCount: number;
    // 是否显示用户编号
    showUserCode = true;

    constructor(
        @Optional() private notifyService: NotifyService,
        @Optional() private favoritesTool: FavoritesTool,
        private injector: Injector
    ) {
        this.allIndexId = Math.random().toString(16).substring(3, 12);
        this.cdRef = this.injector.get(ChangeDetectorRef);
    }

    ngOnInit(): void {
        // 兼容老表单
        if (this.favoritesTool) {
            this.favoritesTool.favoritesSubject.subscribe(data => {
                this.favoritesList = data;
                if (this.enableFavorites) {
                    this.loadSearchResult();
                }
            });
        }
    }

    ngAfterViewInit(): void {
    }

    turnOverPage(dir: string, num: number) {
        if ((dir === 'pre' && this.pageIndex === 1) || (dir === 'next' && this.pageIndex === this.pageTotal)) {
            return;
        }
        this.pageIndex = this.pageIndex + num;
        this.switchPage({ pageIndex: this.pageIndex });
    }

    beforePageIndexChange(inputEl: KeyboardEvent, maxNumber) {
        const target = inputEl.target as any;
        const currentPageNumber = Number(target.value);
        if (currentPageNumber > maxNumber) {
            target.value = maxNumber;
        } else {
            if (currentPageNumber < 1) {
                target.value = 1;
            }
        }
        this.pageIndex = Number(target.value);
    }

    switchPage(size: any) {
        this.pageIndexChange.emit(size);
    }

    closePanel() {
        this.closePanelChange.emit(true);
    }

    // 目前点击触发按钮已被去掉，无法被触发 2022.3.15
    toggleAll() {
        if (!this.allChecked) {
            this.checkAll();
        } else {
            this.unCheckAll();
        }
        this.emitSelections();
    }

    toggleOne(data) {
        if (data.checked) {
            this.unSelectLine(data);
        } else {
            this.selectLine(data);
        }
        this.emitSelections();
    }
    /**
     * 用于数据展示
     */
    loadSearchResult() {
        this.items.forEach(res => {
            const indexId = Math.random().toString(16).substring(3, 10);
            res.checked = false;
            res.inFavorites = false;
            res.indexId = indexId;
            if (this._isInArray(res[this.idField], this.idField, this.selections) !== -1) {
                res.checked = true;
            }
            if (this._isInArray(res[this.idField], this.idField, this.favoritesList) !== -1) {
                res.inFavorites = true;
            }
            res.displayName = !!this.showUserCode ? ('[' + res.code + ']' + res.name) : res.name;
        });
        this.items = this.items.slice();
        this.toggleChecked();
    }

    /**
     * 全选后处理
     * @param dataArr 全选的值
     * 由toggleAll()转调，目前无法被触发 2022.3.15
     */
    checkAll() {
        this.items.forEach(item => {
            this._insertOneIntoSelections(item);
            item.checked = true;
        });
    }

    /**
     * 全部取消
     * @param dataArr 取消的数据
     * @returns void
     * 由toggleAll()转调，目前无法被触发 2022.3.15
     */
    unCheckAll() {
        this.items.forEach(item => {
            this.selections.forEach((selection, index) => {
                if (item[this.idField] === selection[this.idField]) {
                    this.selections.splice(index, 1);
                }
            });
            item.checked = false;
        });
    }

    /**
     * 当前行选中
     * @param data 当前行的数据（）
     */
    selectLine(data: any) {
        if (this.multiSelect) {
            this._insertOneIntoSelections(data);
        } else {
            this.selections = [];
            this.items.forEach(item => {
                if (item.checked) {
                    item.checked = false;
                }
            });
            this._insertOneIntoSelections(data);
        }
        if (this.isAllChecked()) {
            this.allChecked = true;
        }
    }

    /**
     * 单选取消选中
     * @param data 取消选中行
     */
    unSelectLine(data: any) {
        const index = this._isInArray(data[this.idField], this.idField, this.selections);
        if (index > -1) {
            this.selections.splice(index, 1);
        }
        if (this.allChecked) {
            this.allChecked = false;
        }
    }

    private emitSelections() {
        const emitObj = {
            selections: this.selections,
            token: 'recent'
        };
        this.selectionsChange.emit(emitObj);
    }

    toggleFavorites(e: any, item: any) {
        this.favoritesTool.toggleFavorites(e, item);
    }

    /**
     *  取消选中
     * @param $event 取消勾选的数据
     */
    cancelSelections($event) {
        if (this.items.findIndex(item => item[this.idField] === $event.id) === -1) {
            return;
        }
        this.items.forEach(item => {
            if (item[this.idField] === $event.id) {
                item.checked = false;
            }
        });
        this.selections.forEach((selection, index) => {
            if ($event.id === selection[this.idField]) {
                this.selections.splice(index, 1);
            }
        });
        this.allChecked = false;
    }

    /**
     * 全部取消勾选
     */
    clearCheckeds() {
        this.items.forEach(item => {
            item.checked = false;
        });
        this.allChecked = false;
        this.selections = [];
    }

    /**
     * 选中并插入当前行数据到右侧
     * @param data 当前行的数据
     * @param collection 暂存当前数据的集合
     */
    _insertOneIntoSelections(data: any) {
        // 如果当前数据已经移入右侧数据，则返回。
        if (this._isInArray(data[this.idField], this.idField, this.selections) > -1) {
            return;
        }
        this.selections.push(data);
        this.selections = this.selections.slice();
    }

    /**
     * 判断是否在内
     * @param value value
     * @param field field
     * @param array array
     * @returns 位置索引
     */
    _isInArray(value: string, field: string, array: any[]): number {
        if (!field || !value) {
            throw new Error(this.validElementsInfo);
        }
        return array.findIndex(item => value === item[field]);
    }

    /**
     * 翻页检查：人员展示区的勾选状态与已选人员区是否对应
     */
    toggleChecked() {
        this.items.forEach(item => {
            if (this._isInArray(item[this.idField], this.idField, this.selections) > -1) {
                item.checked = true;
            } else {
                item.checked = false;
            }
        });
        if (this.isAllChecked()) {
            this.allChecked = true;
        } else {
            this.allChecked = false;
        }
    }

    private isAllChecked(): boolean {
        let count = 0;
        let allChecked = false;

        this.items.forEach(item => {
            this.selections.forEach(selection => {
                if (item[this.idField] === selection[this.idField]) {
                    count += 1;
                }
            });
        });
        if (this.items.length === count && count > 0) {
            allChecked = true;
        }
        return allChecked;
    }

    trackByFn(index, item) {
        return index;
    }

}
