import { AfterViewInit, Component, EventEmitter, Inject, Input, OnInit, Optional, Output, ViewChild } from '@angular/core';
import * as _ from 'lodash-es';
import { noDataImg } from '../../assets/resource';
import { GROUP_SERVICE } from '../../utils/app.token';
import { FavoritesTool } from '../../utils/favorites.tool';
import { Group, GroupItem } from '../../utils/group.tool';
import { OrgPersonnelComponent } from '../tab-org-personnel/op.component';
import { DataLoadingService } from '../../utils/loading-service';

declare const method: any;
@Component({
    selector: 'lib-group',
    templateUrl: './group.component.html',
    styleUrls: ['./group.component.css'],
    // providers: [{
    //     provide: GROUP_SERVICE,
    //     useClass: GroupTool
    // }]
})
export class GroupComponent implements OnInit, AfterViewInit {

    @ViewChild('groupPersonnels') groupPersonnelComponent: OrgPersonnelComponent;

    @Input() idField = 'userId';
    @Input() multiSelect: any;
    @Input() absOrgType: string;
    @Input() searchText: any;
    @Input() orgId = '';
    @Input() userIds = '';
    @Input() includeStopUser: boolean;
    @Input() searchPage: boolean;
    @Input() includeCurrentUser: boolean;
    @Input() busNum: string;
    @Input() enableFavorites: boolean;

    @Input() pageSize = 10;
    @Input() userTypeMode: string;
    @Input() orderField: string;
    @Input() enablePagination: boolean;
    @Input() absUserOrgUrl: string;
    @Input() treeListData = [];
    @Input() orgIdField: string;
    @Input() enableHierarchicalLoading: boolean;
    @Input() includeStopOrg: boolean;
    @Input() originalTreeData: any;
    @Input() personnelOrderField: string;
    @Input() filterId: string;
    @Input() dataLoadingService: DataLoadingService;
    @Input() validElementsInfo: string;
    @Input() groupExtendInfo: string;
    @Input() withPermission: boolean;
    @Output() selectionsChange = new EventEmitter<any>();
    @Output() groupChange = new EventEmitter<any>();

    groupIdField = 'id';
    userIdField = 'userId';
    selections: any[] = [];
    cloneUserIds = '';
    cloneSelections = [];
    noDataImg = noDataImg;
    allChecked = false;
    items = [];
    favoritesList = [];
    groupData: Group = { public: [], personal: [] };
    personnelList = [];
    groupPersonnels = [];
    publicVisible = true;
    personalVisible = true;
    publicToken = 'public';
    personalToken = 'personal';
    selectedGroupId: string;
    allIndexId: string;
    showGroupPersonnelDialog = false;
    currentGroupType = 'public';
    // 是否显示用户编号
    showUserCode = true;
    loadingServiceMessage: string;

    constructor(
        @Optional() @Inject(GROUP_SERVICE) private groupService,
        @Optional() private favoritesTool: FavoritesTool
    ) {
        this.allIndexId = Math.random().toString(16).substring(3, 10)
    }

    ngOnInit(): void {
        // 兼容老表单
        if (this.favoritesTool) {
            this.favoritesTool.favoritesSubject.subscribe(data => {
                this.favoritesList = data;
                if (this.enableFavorites && !!this.selectedGroupId) {
                    this.getPersonnelsByGroup({ id: this.selectedGroupId }, this.currentGroupType);
                }
            });
        }
    }

    ngAfterViewInit(): void {

    }

    editGroup(e: MouseEvent, item: GroupItem) {
        this.groupChange.next(item);
        e.stopPropagation();
        e.cancelBubble = true;
    }

    //群组触发事件类型 1：add 2：delete 3：update(title和description) 4：update(追加群组成员)
    deleteGroup(e: MouseEvent, item: GroupItem) {
        const helpExtend = window && (window as any).helpExtend;
        if (item[this.groupIdField] === this.selectedGroupId) {
            this.selectedGroupId = null;
            this.currentGroupType = this.publicToken;
        }
        if (helpExtend) {
            helpExtend.curdGroupData({
                id: item[this.groupIdField],
                type: 2
            }).then(() => {
                this.items = [];
                this.getGroupList();
            });
        } else {
            throw new Error(this.groupExtendInfo);
        }
        e.stopPropagation();
        e.cancelBubble = true;
    }

    /**
     * 添加群组成员
     */
    appendGroupPersonnel() {
        const helpExtend = window && (window as any).helpExtend;
        const userIds = [];
        this.groupPersonnelComponent.selections.forEach(selection => {
            userIds.push(selection[this.idField]);
        });
        if (helpExtend) {
            helpExtend.curdGroupData({
                id: this.selectedGroupId,
                userIds,
                type: 4
            }).then(() => {
                this.toggleGroupPersonnelDialog(false);
                this.getPersonnelsByGroup({ id: this.selectedGroupId }, this.currentGroupType);

            });
        } else {
            throw new Error(this.groupExtendInfo);
        }
    }

    toggleGroupPersonnelDialog(show: boolean, groupType = this.personalToken) {
        if (groupType === this.personalToken) {
            this.showGroupPersonnelDialog = show;
        }
    }

    // 设置我的群组
    // setMyGroup() {
    //     const options = {
    //         // 打开菜单，funcId需要指定，appId、appEntrance可以为空
    //         funcId: '2cea1078-eefb-b716-37b5-480afcbede03',
    //         tabId: '个人群组',
    //         appId: '',
    //         appEntrance: '',
    //         // 指定当前打开的是菜单
    //         appType: 'menu'
    //     };
    //     (window as any).gspframeworkService.rtf.func.openMenu(options);
    // }

    assembleData() {
        if (this.searchPage) {
            this.searchByCodeOrName();
        } else {
            this.getGroupList();
        }
    }

    getPersonnelsByGroup(item: any, groupType = this.publicToken) {
        if(this.dataLoadingService) {
            this.dataLoadingService.show(this.loadingServiceMessage);
        }
        this.currentGroupType = groupType;
        this.allChecked = false;
        this.selectedGroupId = item[this.groupIdField];
        const helpExtend = window && (window as any).helpExtend;
        if (helpExtend) {
            helpExtend.getDataByGroupId(item[this.groupIdField], this.absOrgType).then(data => {
                const res = data && data.items;
                this.items = res;
                this.groupData.personal.forEach(item => {
                    if (item[this.groupIdField] === this.selectedGroupId && item.total !== this.items.length) {
                        item.total = this.items.length;
                    }
                });
                this.items.forEach(it => {
                    if (this._isInArray(it[this.idField], this.idField, this.favoritesList) !== -1) {
                        it.inFavorites = true;
                    } else {
                        it.inFavorites = false;
                    }
                    it.indexId = Math.random().toString(16).substring(2);
                    it.displayName = !!this.showUserCode ? ('[' + it.code + ']' + it.name) : it.name;
                });
                this.toggleChecked();
                if(this.dataLoadingService) {
                    this.dataLoadingService.hide();    
                }
            });
        } else {
            throw new Error(this.groupExtendInfo);
        }
    }

    getGroupList() {
        const helpExtend = window && (window as any).helpExtend;
        if (helpExtend) {
            helpExtend.getGroupData().then(data => {
                const resPublic = data && data.items[0] && data.items[0].data;
                const resPersonal = data && data.items[1] && data.items[1].data;
                this.groupData.public = resPublic ? resPublic : [];
                this.groupData.personal = resPersonal ? resPersonal : [];
                const firstPublicGroup = resPublic[0] && resPublic[0][this.groupIdField];
                const firstPersonalGroup = resPersonal[0] && resPersonal[0][this.groupIdField];
                this.selectedGroupId = this.selectedGroupId ? this.selectedGroupId : firstPublicGroup;
                this.selectedGroupId = this.selectedGroupId ? this.selectedGroupId : firstPersonalGroup;
                if (!!this.selectedGroupId) {
                    this.getPersonnelsByGroup({ id: this.selectedGroupId }, this.currentGroupType);
                    if (this.groupData.public.findIndex(item => item[this.groupIdField] === this.selectedGroupId) !== -1) {
                        this.currentGroupType = this.publicToken;
                    } else if (this.groupData.personal.findIndex(item => item[this.groupIdField] === this.selectedGroupId) !== -1) {
                        this.currentGroupType = this.personalToken;
                    }
                }
            });
        } else {
            throw new Error(this.groupExtendInfo);
        }
    }

    searchByCodeOrName() {
        this.items = this.items.filter(item => {
            return item.code.indexOf(this.searchText) !== -1 || item.name.indexOf(this.searchText) !== -1;
        });
    }

    toggleFavorites(e: any, item: any) {
        this.favoritesTool.toggleFavorites(e, item);
    }

    toggleVisible(e, type: string) {
        if (type === this.publicToken) {
            this.publicVisible = !this.publicVisible;
        } else if (type === this.personalToken) {
            this.personalVisible = !this.personalVisible;
        }
        e.stopPropagation();
        e.cancelBubble = true;
    }

    restoreSelections() {
        this.selections = _.cloneDeep(this.cloneSelections);
        // this.emitSelections();
        this.toggleChecked();
        if (this.isAllChecked()) {
            this.allChecked = true;
        } else {
            this.allChecked = false;
        }
    }

    toggleAll() {
        if (!this.allChecked) {
            this.checkAll();
        } else {
            this.unCheckAll();
        }
        this.emitSelections();
    }

    toggleOne(data) {
        if (data.checked) {
            this.unSelectLine(data);
        } else {
            this.selectLine(data);
        }
        this.emitSelections();
    }
    /**
     * datagrid用于数据展示
     */
    loadGroupList() {
        this.items = [];
        // this.unSelectedList.forEach(res => {
        //     const indexId = Math.random().toString(16).substring(3, 10);
        //     const item = {
        //         abbreviation: res.abbreviation,
        //         code: res.code,
        //         defaultOrgId: res.defaultOrgId,
        //         defaultOrgPath: res.defaultOrgPath,
        //         ext1: res.ext1,
        //         ext2: res.ext2,
        //         fullPathName: res.fullPathName,
        //         name: res.name,
        //         orgTypeId: res.orgTypeId,
        //         secLevel: res.secLevel,
        //         sortOrder: res.sortOrder,
        //         state: res.state,
        //         su: res.su,
        //         userId: res.userId,
        //         orgAllPath: res.orgAllPath,
        //         type: 'people',
        //         checked: false,
        //         inFavorites: false,
        //         indexId
        //     };
        //     if (this._isInArray(res[this.idField], this.idField, this.selections) !== -1) {
        //         item.checked = true;
        //     }
        //     if (this._isInArray(res[this.idField], this.idField, this.favoritesList) !== -1) {
        //         item.inFavorites = true;
        //     }
        //     this.items.push(item);
        //     this.itemsBackup.push(item);
        // });
        const tempSelections = _.cloneDeep(this.selections);
        this.toggleChecked();
        this.selections = _.cloneDeep(tempSelections);
    }

    /**
     * 全选后处理
     * @param dataArr 全选的值
     */
    checkAll() {
        this.items.forEach(item => {
            this._insertOneIntoSelections(item);
            item.checked = true;
        });
    }

    /**
     * 全部取消
     * @param dataArr 取消的数据
     * @returns void
     */
    unCheckAll() {
        this.items.forEach(item => {
            this.selections.forEach((selection, index) => {
                if (item[this.idField] === selection[this.idField]) {
                    this.selections.splice(index, 1);
                }
            });
            item.checked = false;
        });
    }

    /**
     * 当前行选中
     * @param data 当前行的数据（）
     */
    selectLine(data: any) {
        if (this.multiSelect) {
            this._insertOneIntoSelections(data);
        } else {
            this.userIds = '';
            this.selections = [];
            this.items.forEach(item => {
                if (item.checked) {
                    item.checked = false;
                }
            });
            this._insertOneIntoSelections(data);
        }
        if (this.isAllChecked()) {
            this.allChecked = true;
        }
    }

    /**
     * 单选取消选中
     * @param data 取消选中行
     */
    unSelectLine(data: any) {
        const index = this._isInArray(data[this.idField], this.idField, this.selections);
        if (index > -1) {
            this.selections.splice(index, 1);
        }
        if (this.allChecked) {
            this.allChecked = false;
        }
    }

    private emitSelections() {
        const emitObj = {
            selections: this.selections,
            token: 'candidate'
        };
        this.selectionsChange.emit(emitObj);
    }

    /**
     *  取消选中
     * @param $event 取消勾选的数据
     */
    cancelSelections($event) {
        if (this.items.findIndex(item => item[this.idField] === $event.id) === -1) {
            return;
        }
        this.items.forEach(item => {
            if (item[this.idField] === $event.id) {
                item.checked = false;
            }
        });
        this.selections.forEach((selection, index) => {
            if ($event.id === selection[this.idField]) {
                this.selections.splice(index, 1);
            }
        });
        this.allChecked = false;
    }

    /**
     * 全部取消勾选
     */
    clearCheckeds() {
        this.items.forEach(item => {
            item.checked = false;
        });
        this.allChecked = false;
        this.selections = [];
    }

    clearUserIds() {
        this.userIds = '';
    }
    /**
     * 选中并插入当前行数据到右侧
     * @param data 当前行的数据
     * @param collection 暂存当前数据的集合
     */
    _insertOneIntoSelections(data: any) {
        // 如果当前数据已经移入右侧数据，则返回。
        if (this._isInArray(data[this.idField], this.idField, this.selections) > -1) {
            return;
        }
        this.selections.push(data);
        this.selections = this.selections.slice();
    }

    /**
     * 判断是否在内
     * @param value value
     * @param field field
     * @param array array
     * @returns 位置索引
     */
    _isInArray(value: string, field: string, array: any[]): number {
        if (!field || !value) {
            throw new Error(this.validElementsInfo);
        }
        return array.findIndex(item => value === item[field]);
    }

    /**
     * 翻页检查：人员展示区的勾选状态与已选人员区是否对应
     */
    toggleChecked() {
        this.items.forEach(item => {
            if (this._isInArray(item[this.idField], this.idField, this.selections) > -1) {
                item.checked = true;
            } else {
                item.checked = false;
            }
        });
        if (this.isAllChecked()) {
            this.allChecked = true;
        } else {
            this.allChecked = false;
        }
    }

    private isAllChecked(): boolean {
        let count = 0;
        let allChecked = false;

        this.items.forEach(item => {
            this.selections.forEach(selection => {
                if (item[this.idField] === selection[this.idField]) {
                    count += 1;
                }
            });
        });
        if (this.items.length === count && count > 0) {
            allChecked = true;
        }
        return allChecked;
    }

    trackByFn(index, item) {
        return index;
    }

}
