import { Injectable } from '@angular/core';
import { Observable, Subject } from 'rxjs';
@Injectable()
export class FilterOrgTool {
    private selectOrgSubject: Subject<string> = new Subject<string>();

    constructor( ) { }

    get filterOrgSubject(): Observable<string> {
        return this.selectOrgSubject.asObservable();
    }

    emitFilterOrg(value: any) {
        this.selectOrgSubject.next(value);
    }
}
