    constructor(
        private apiService: ApiLocalService,
        private injector: Injector,
        @Optional() private favoritesTool: FavoritesTool,
        @Optional() private eventManager: EventManager,
        @Optional() private groupTool: GroupTool,
        @Optional() private cd: ChangeDetectorRef
    ) {
        this.indexId = Math.random().toString(16).substring(3);
        this.msgService = this.injector.get(MessagerService);
        this.notifyService = this.injector.get(NotifyService);
        this.inputElementId = 'sslc' + this.indexId;
        this.hostElement = this.injector.get(ElementRef);
        this.http = this.injector.get(HttpClient);
        this.cdRef = this.injector.get(ChangeDetectorRef);
        this.render = this.injector.get(Renderer2);
        this.ngZone = this.injector.get(NgZone);
        this.appRef = this.injector.get(ApplicationRef);
        
        this.loadingService = this.injector.get(LoadingService, null);
        if (this.loadingService) {
            this.dataLoadingService = new DataLoadingService();
            this.dataLoadingService.loadingService = this.loadingService;
        }

        this.localeService = this.injector.get(LocaleService, null);
        if(!this.localeService.langData['personnelSelector']) {
            this.localeService.setLocaleData(PERSONNEL_SELECTOR_LOCALE);
        }
    }

    onModelChange: any = (val?: any) => { };
    onModelTouched: any = (val?: any) => { };

    writeValue(obj: any): void {
        if (this.viewType === 'tag') {
            if (!!obj && typeof obj === 'string') {
                this.finalText = obj.split(this.multipleChoiceSeparator);
            } else {
                this.finalText = [];
            }
        } else if (this.viewType === 'text') {
            this.finalText = obj;
        }
        if(this.enableQuick){
            this.setQuickContainerWidth();
        }
    }

    registerOnChange(fn: any): void {
        this.onModelChange = fn;
    }
    registerOnTouched(fn: any): void {
        this.onModelTouched = fn;
    }
    setDisabledState?(isDisabled: boolean): void {
        throw new Error('Method not implemented.');
    }

    ngOnInit(): void {
        this.clearButid = this.indexId + 'clearBu';
        this.selectAllPersonnelIndexId = this.indexId + 'selectAllPersonnel';
        this.selectAllOrganizationIndexId = this.indexId + 'selectAllOrganization';
        this.selectLatelyPersonnelIndexId = this.indexId + 'selectLatelyPersonnel';
        this.quickContainerId = 'qc' + this.indexId;
        this.tagContainerId = 't-c' + this.indexId;
        this.publicGroup = 'group-checkbox' + this.indexId;
        this.dialogId = 'dialogid' + this.indexId;
        this._enableChildSysUserTree = this.enableChildHierarchy;
        this.pIncludeCurrentUser = this.includeCurrentUser;
        if(!this.showTabIds.includes(this.activeTabId)) {
            this.activeTabId = this.showTabIds.split(',')[0];
        }

        this.initDisplayTab();
        // 决定tab页的显示或隐藏
        this.checkDisplayTab();

        this.ngControl = this.injector.get(NgControl, null);
        
        //this.setInputGroupElementWith();
        this.initLocaleItems();

        this.initRegisterEvent();

        const windowInnerHeight = window.innerHeight;

        this.dialogHeight = windowInnerHeight - 60;
        if (this.dialogHeight > 680) {
            this.dialogHeight = 680;
        }

        this.groupTitleStream.pipe(
            debounceTime(500),
            distinctUntilChanged()
        ).subscribe(data => {
            if (!!data) {
                this.validGroupTitle = true;
            } else {
                this.validGroupTitle = false;
            }
        })

        this.treeTableCols = [
            { field: 'name', title: 'Name', width: 200, template: this.orgTreeCell }
        ];
        this.columnsOrg = [
            {
                field: 'name', template: this.opCellOrg, title: '编辑', width: '150',
            }
        ];
        // 鼠标点击事件关闭组织搜索结果列表 
        this.documentClick_Org = (event: any) => {
            if(this.isOnTop) {
                if (!!top.document.querySelector('.s-orgtree-search-result')) {
                    let parentNodeTitle_one = event.target['parentNode']['parentNode']['parentNode']['title'];
                    let parentNodeTitle_two = event.target['parentNode']['parentNode']['parentNode']['parentNode']['title'];
                    if(parentNodeTitle_one !== 'locationOrgList_title' && parentNodeTitle_two !== 'locationOrgList_title') {
                        this.searchOrgElementVisible = false; 
                        this.showUnSelectableInfo = false;
                    }
                }
            } else {
                if (!!document.querySelector('.s-orgtree-search-result')) {
                    let parentNodeTitle_one = event.target['parentNode']['parentNode']['parentNode']['title'];
                    let parentNodeTitle_two = event.target['parentNode']['parentNode']['parentNode']['parentNode']['title'];
                    if(parentNodeTitle_one !== 'locationOrgList_title' && parentNodeTitle_two !== 'locationOrgList_title') {
                        this.searchOrgElementVisible = false; 
                        this.showUnSelectableInfo = false;
                    }
                }
            }
        }
        // 鼠标点击事件关闭用户分组搜索结果列表
        this.documentClick_SysUerGroup = (event: any) => {
            if(this.isOnTop) {
                if (!!top.document.querySelector('.s-usergrouptree-search-result')) {
                    let parentNodeTitle_one = event.target['parentNode']['parentNode']['parentNode']['title'];
                    let parentNodeTitle_two = event.target['parentNode']['parentNode']['parentNode']['parentNode']['title'];
                    if(parentNodeTitle_one !== 'locationSysUserGroupList_title' && parentNodeTitle_two !== 'locationSysUserGroupList_title') {
                        this.searchSysUserGroupElementVisible = false;
                    }
                }
            } else {
                if (!!document.querySelector('.s-usergrouptree-search-result')) {
                    let parentNodeTitle_one = event.target['parentNode']['parentNode']['parentNode']['title'];
                    let parentNodeTitle_two = event.target['parentNode']['parentNode']['parentNode']['parentNode']['title'];
                    if(parentNodeTitle_one !== 'locationSysUserGroupList_title' && parentNodeTitle_two !== 'locationSysUserGroupList_title') {
                        this.searchSysUserGroupElementVisible = false;
                    }
                }
            }
        }
    }

