    // 打开弹窗时调用，初始化是否过滤领导
    initGroupLeaderStatus() {
        const param = {
            absOrgType: this.absOrgType,
            bizId: this.filterId,
            objectValue: 'FilterUser'
        }
        this.apiService.getGroupLeaderStatus(param).subscribe((data: any) => {
            if (data && data.length) {
                this.showGroupLeader = data[0].textvalue === '0' ? false : true;
                if (data[0].textvalue === '1') {
                    this.includeGroupLeader = true;
                    this.pIncludeGroupLeader = true;
                } else if (data[0].textvalue === '2') {
                    this.includeGroupLeader = false;
                    this.pIncludeGroupLeader = false;
                }
            }
        })
    }

    /**
     * 根据组织id构建自定义组织树, 后端提供了接口后就废弃
     * 加载组织树的数据时，若orgId为特定组织的id值，则只加载该组织及其直接上级
     * @param array 原始数据
     * @param orgId 组织id
     */
    private assembleCustomTreeData(array: any[], orgId: string) {
        this.customTreeData = [];
        const currentNode = this.getCurOrgInfoById(array, orgId);
        this.getParentsById(array, currentNode, this.customTreeData);
        this.getChildrenById(array, currentNode, this.customTreeData);
        this.treeListData = this.dataToTree(this.customTreeData, 1, currentNode.path);
    }

     /**
     * 根据用户分组id构建自定义用户分组树, 后端提供了接口后就废弃
     * 加载用户分组树的数据时，若userGroupId为特定用户分组的id值，则只加载该用户分组及其直接上级
     * @param array 原始数据
     * @param groupId 用户分组id
     */
    private assembleCustomSysUserGroupTreeData(array: any[], groupId: string) {
        this.customSysUserGroupTreeData = [];
        const currentNode = this.getCurUserGroupInfoById(array, groupId);
        this.getSysUserGroupParentsById(array, currentNode, this.customSysUserGroupTreeData);
        this.getSysUserGroupChildrenById(array, currentNode, this.customSysUserGroupTreeData);
        this.sysUserGroupTreeListData = this.sysUserGroupDataToTree(this.customSysUserGroupTreeData, 1, currentNode.path);
    }

    /**
     * 根据组织id获取当前组织节点信息
     * 被调用之处：assembleCustomTreeData()
     * @param array 原始数据
     * @param id 组织id
     * @returns obj
     */
    private getCurOrgInfoById(array: any[], id: string) {
        let orgNode: any;
        for (const item of array) {
            if (item.orgId === id) {
                orgNode = item;
                break;
            }
        }
        if (!orgNode) {
            this.msgService.warning(this.localeService.getValue("personnelSelector.message.inappropriateData"));
            return orgNode;
        }
        orgNode.selectable = true;
        this.customTreeData.push(orgNode);
        return orgNode;
    }

    /**
     * 根据用户分组id获取当前用户分组节点信息
     * 被调用之处：assembleCustomSysUserGroupTreeData()
     * @param array 原始数据
     * @param id 用户分组id
     * @returns obj
     */
    private getCurUserGroupInfoById(array: any[], id: string) {
        let userGroupNode: any;
        for (const item of array) {
            if (item.id === id) {
                userGroupNode = item;
                break;
            }
        }
        if (!userGroupNode) {
            this.msgService.warning(this.localeService.getValue("personnelSelector.message.inappropriateData"));
            return userGroupNode;
        }
        userGroupNode.selectable = true;
        this.customSysUserGroupTreeData.push(userGroupNode);
        return userGroupNode;
    }

    /**
     * 根据节点信息获取到集团下单位的父级元素
     * 被调用之处：assembleLocationOrgList() 返回搜索后的组织列表
     * @param array 原始数据
     * @param curNode 节点信息
     * @returns null
     */
    private getUnitParentsById(array: any[], curNode: any, resArray: any[]) {
        const parents = array.filter(item => {
            return Number(item.layer) === (Number(curNode.layer) - 1);
        });
        if (!parents) {
            return;
        }
        for (const item of parents) {
            if (item.orgId === curNode.parentId && curNode.valueType === 'depttype') {
                item.selectable = false;
                resArray.push(item);
                if (item.valueType === 'depttype') {
                    this.getUnitParentsById(array, item, resArray);
                }
                break;
            }
        }
    }

    /**
     * 根据节点信息获取特定组织的所有父级元素
     * 被调用之处：assembleCustomTreeData()
     * @param array 原始数据
     * @param curNode 节点信息
     * @returns null
     */
    private getParentsById(array: any[], curNode: any, resArray: any[]) {
        const parents = array.filter(item => {
            return Number(item.layer) === (Number(curNode.layer) - 1);
        });
        if (!parents) {
            return;
        }
        for (const item of parents) {
            if (item.orgId === curNode.parentId || curNode.parentId === '') {
                item.selectable = false;
                resArray.push(item);
                this.getParentsById(array, item, resArray);
                break;
            }
        }
    }

    /**
     * 根据节点信息获取特定组织的所有子级元素
     * 被调用之处：assembleCustomTreeData()
     * @param array 原始数据
     * @param curNode 节点信息
     * @returns null
     */
    private getChildrenById(array: any[], curNode: any, resArray: any[]) {
        const children = array.filter(item => {
            return Number(item.layer) === (Number(curNode.layer) + 1);
        });
        if (!children) {
            return;
        }
        for (const item of children) {
            if (item.parentId === curNode.orgId) {
                item.selectable = this._onlyCurrentOrg ? false : true;
                resArray.push(item);
                this.getChildrenById(array, item, resArray);
            }
        }
    }

    /**
     * 根据节点信息获取特定用户分组的所有父级元素
     * 被调用之处：assembleCustomSysUserGroupTreeData()
     * @param array 原始数据
     * @param curNode 节点信息
     * @returns null
     */
     private getSysUserGroupParentsById(array: any[], curNode: any, resArray: any[]) {
        const parents = array.filter(item => {
            return Number(item.layer) === (Number(curNode.layer) - 1);
        });
        if (!parents) {
            return;
        }
        for (const item of parents) {
            if (item.id === curNode.parentId || curNode.parentId === '') {
                item.selectable = false;
                resArray.push(item);
                this.getSysUserGroupParentsById(array, item, resArray);
                break;
            }
        }
    }

    /**
     * 根据节点信息获取特定用户分组的所有子级元素
     * 被调用之处：assembleCustomSysUserGroupTreeData()
     * @param array 原始数据
     * @param curNode 节点信息
     * @returns null
     */
    private getSysUserGroupChildrenById(array: any[], curNode: any, resArray: any[]) {
        const children = array.filter(item => {
            return Number(item.layer) === (Number(curNode.layer) + 1);
        });
        if (!children) {
            return;
        }
        for (const item of children) {
            if (item.parentId === curNode.id) {
                item.selectable = true;
                resArray.push(item);
                this.getSysUserGroupChildrenById(array, item, resArray);
            }
        }
    }

