    // 监听候选区变化
    listenSelectionsChange(obj: any) {
        if (obj.selections && obj.selections instanceof Array) {
            this.selections = _.cloneDeep(obj.selections);
            this.selections.forEach(item => {
                item.displayName = !!this.showUserCode ? ('[' + item.code + ']' + item.name) : item.name;
            });
            this.cdRef.detectChanges();
            this.componentMap.forEach(component => {
                if(component['cdRef']){
                    component['cdRef']['detectChanges']();
                }
            });
        }
        if (obj.token && obj.token === 'quick') {
            this.quickPanelVisible = false;
            if (this.recentContactsList.length === 0) {
                this.recentContactsList = _.cloneDeep(obj.recentContactsList);
            }
            this.returnResult(1);
        }
    }

