    /**
     * 数据转换
     * @param parents 父级节点信息
     * @param treeSource 原始数据信息
     * @param layer 层级
     */
    treeData(parents: any, treeSource: any, layer: number) {
        parents.forEach(parent => {
            const parentPath = parent['data']['path'];
            const parentLayer = parent['data']['layer'];
            treeSource.forEach(item => {
                if (item && item['data'] && item['data']['path']) {
                    const itemPath = item['data']['path'];
                    const itemLayer = item['data']['layer'];
                    let targetPath: string;
                    if (itemPath && itemPath.length > parentPath.length) {
                        targetPath = itemPath.substr(0, Number(layer) * 4);
                    }
                    if (parentPath === targetPath && parentLayer === itemLayer - 1) {
                        parent['children'].push(item);
                    }
                    if (parentPath === targetPath) {
                        this.treeData([item], treeSource, Number(layer) + 1);
                    }
                }
            });

        })
    }

    // 关闭人员选择弹窗时，确认回填至文本框的人员展示文本
    private assembleFinalText() {
        this.finalText = [];
        this.selections.forEach((item, index) => {
            const displayName = this.formatFn && this.formatFn(item);
            let expressionName = '';
            if (typeof this.expression === 'string' && this.expression.length) {
                expressionName = this.expression.replace(/{{(.*?)}}/g, (...args) => {
                    return item[args[1]] ? item[args[1]] : '';
                });
            }
            let codeAndName = '';
            if (item.code && this.showUserCode) {
                codeAndName += '[' + item.code + ']';
            }
            if (item.name) {
                codeAndName += item.name;
            }
            const defaultDisplayName = !!this.displayField ? item[this.displayField] : codeAndName;
            if (this.viewType === 'text') {
                if (!!this.formatFn) {
                    this.finalText += displayName + ',';
                } else if (!!this.expression) {
                    this.finalText += expressionName + ',';
                } else {
                    this.finalText += defaultDisplayName + ',';
                }
            } else if (this.viewType === 'tag') {
                if (!!this.formatFn) {
                    this.finalText.push(displayName);
                } else if (!!this.expression) {
                    this.finalText.push(expressionName);
                } else {
                    this.finalText.push(defaultDisplayName);
                }
            }
        });
        if (this.viewType === 'text' && this.finalText.length) {
            this.finalText = this.finalText.substring(0, this.finalText.length - 1);
        }
        
        if (this.enableQuick) {
            this.setQuickContainerWidth();
        }
    }

    // 增加最近联系人
    private addRecentContacts() {
        const param: RfRequestParam = {
            absOrgType: this.absOrgType,
            bizId: this.busNum,
            objectValue: 'RecentContacts'
        };

        if (!this.selections.length) {
            return;
        }
        const reverseSelections = [];
        for (let i = this.selections.length - 1; i >= 0; i--) {
            reverseSelections.push(this.selections[i]);
        }
        const top10Selections = reverseSelections.slice(0, this.recentContactsLimit);
        top10Selections.forEach(item => {
            const index = this.recentContactsList.findIndex(rit => rit === item[this.idField]);
            if (index !== -1) {
                this.recentContactsList.splice(index, 1);
            }
            this.recentContactsList.unshift(item[this.idField]);
        });
        this.recentContactsList = this.recentContactsList.slice(0, this.recentContactsLimit);
        param.addList = this.recentContactsList.slice();
        this.favoritesTool.curdRecentContactsOrFavorites('modify', param).subscribe(() => {

        }, error => {
            // UX说出错不用给出提示。
            // this.msgService.show('error', 'some error occured', {
            //     height: 140,
            //     showHeader: false
            // });
        });
    }

    // 初始化收藏人员数据
    private initFavorites() {
        if (this.enableFavorites) {
            this.favoritesTool.absOrgType = this.absOrgType;
            this.favoritesTool.busNum = this.busNum;
            this.favoritesTool.enableFavorites = this.enableFavorites;
            this.favoritesTool.idField = this.idField;
            this.favoritesTool.getFavoritesList().subscribe((result) => {
                const resData = result ? (result as any).data : [];
                this.favoritesTool.favoritesList = resData;
                this.favoritesTool.setFavoritesList(resData);
            });
        }
    }

    /**
     * 选择完人后，点击确认、取消按钮的逻辑
     * @param numberType 0：取消；1：确定
     */
    returnResult(numberType: number) {
        let finalText: any;
        if (numberType) {
            this.cloneSelections = _.cloneDeep(this.selections);
            this.confirmToken = true;
            this.assembleFinalText();
            // 兼容旧表单
            if (this.favoritesTool) {
                this.addRecentContacts();
            }
            if (this.selections && this.selections.length) {
                this.selectionsChange.emit({
                    info: 'confirm',
                    data: this.selections,
                    token: this['token']
                });
                if (this.finalText instanceof Array) {
                    finalText = this.finalText.join();
                } else if (typeof this.finalText === 'string') {
                    finalText = this.finalText;
                }
                this.onModelChange(finalText);
                this.onModelTouched(finalText);
            } else {
                if (this.finalText instanceof Array) {
                    finalText = this.finalText.join();
                } else if (typeof this.finalText === 'string') {
                    finalText = this.finalText;
                }
                this.onModelChange(finalText);
                this.onModelTouched(finalText);
                this.selectionsChange.emit({
                    info: 'confirm',
                    data: this.selections,
                    token: this['token']
                });
            }

            this.quickInput = '';
        } else {
            this.selections = this.selectionList;
            this.selectionsChangeUn.emit({
                info: 'cancel',
                data: this.selections,
                token: this['token']
            });
        }
        this.farrisDialog.close();
    }

