import {
    Component, Input, EventEmitter, Output, ViewChild, OnInit, AfterViewInit, TemplateRef,
    SimpleChanges, OnChanges, Optional, ChangeDetectorRef
} from '@angular/core';
import { ApiLocalService } from '../../utils/api-service';
import * as _ from 'lodash-es';
import { FavoritesTool } from '../../utils/favorites.tool';
import { TreeNode, TreeTableComponent } from '@farris/ui-treetable';
import { GroupTool } from '../../utils/group.tool';
import { OrgRequestParam } from '../../utils/user.type';
import { SearchResultComponent } from '../search-result-page/srp.component';

@Component({
    selector: 'lib-org-personnel',
    templateUrl: './op.component.html',
    styleUrls: ['./op.component.css']
})
export class OrgPersonnelComponent implements OnInit, AfterViewInit, OnChanges {

    @ViewChild('farrisTreetable') farrisTree: TreeTableComponent;
    @ViewChild('orgTreeCell') orgTreeCell: TemplateRef<any>;
    @Input() idField: string;
    @Input() absUserOrgUrl: any;
    @Input() absOrgType: any;
    pageIndex = 1;
    @Input() pageSize: any;
    pageTotalAll: number;
    @Input() multiSelect: any;
    pageTotal: number;
    @Input() favoritesList = [];
    set enableChildHierarchy(value: boolean) {
        this.pEnableChildHierarchy = value;
        this.switchPage({ pageIndex: 1 });
    }
    get enableChildHierarchy() {
        return this.pEnableChildHierarchy;
    }
    @Input() includeStopUser: boolean;
    set includeCurrentUser(value: boolean) {
        this.pIncludeCurrentUser = value;
        this.switchPage({ pageIndex: 1 });
    }
    get includeCurrentUser() {
        return this.pIncludeCurrentUser;
    }
    @Input() enablePagination = true;
    @Input() enableFavorites: boolean;
    @Input() orderField: string;
    @Input() userTypeMode: string;
    @Input() orgIdField = 'orgId';
    @Input() orgId: string;
    @Input() treeListData = [];
    @Input() enableHierarchicalLoading: boolean;
    @Input() includeStopOrg: boolean;
    @Input() originalTreeData: any;
    @Input() personnelOrderField: string;
    @Input() filterId: string;
    @Input() showUserCode = true;
    @Input() withPermission: boolean;
    @Output() selectionsChange = new EventEmitter<any>();

    items = [];
    selections = [];

    private pEnableChildHierarchy = true;
    private pIncludeCurrentUser = true;

    locationOrgText = '';
    searchOrgElementVisible = false;
    locationOrgList = [];
    treeTableCols = [];

    allChecked = false;
    showGroupPersonnelDialog = false;
    selectedGroupId = '';
    searchPlaceholder = '请输入姓名汉字/首字母/编号搜索';
    groupIcon = '<span class="k-icon k-i-search"></span>';
    searchText = '';
    // 进行搜索时，组织选中当前节点，避免再次触发请求数据
    stopNodeSelected = false;
    // 
    onSearching = false;

    constructor(
        @Optional() private apiService: ApiLocalService,
        @Optional() private favoritesTool: FavoritesTool,
        @Optional() private groupTool: GroupTool,
        @Optional() private cd: ChangeDetectorRef
    ) { }

    ngOnInit(): void {
        if (this.favoritesTool) {
            this.favoritesList = this.favoritesTool.favoritesList;
            this.favoritesTool.favoritesSubject.subscribe(data => {
                this.favoritesList = data;
                this.unSelectedListLoad();
            });
        }
        this.treeTableCols = [
            { field: 'name', title: 'Name', width: 200, template: this.orgTreeCell }
        ];
        this.switchPage({ pageIndex: 1 });
        this.treeListData = _.cloneDeep(this.treeListData);
        this.originalTreeData = _.cloneDeep(this.originalTreeData);
    }

    ngAfterViewInit(): void {
    }

    ngOnChanges(changes: SimpleChanges): void {
        if (changes.includeCurrentUser && typeof changes.includeCurrentUser.currentValue === 'boolean'
            && !changes.includeCurrentUser.firstChange) {
            this.assembleDataByOrgId();
        }
        if (changes.enableChildHierarchy && typeof changes.enableChildHierarchy.currentValue === 'boolean'
            && !changes.enableChildHierarchy.firstChange) {
            this.assembleDataByOrgId();
        }
    }

    /**
         * 全局搜索框
         */
    onSearch(size: any) {
        if (!this.searchText) {
            return;
        }
        this.pageIndex = size.pageIndex;
        this.onSearching = true;
        const requestParam: any = {
            pageSize: this.pageSize,
            pageIndex: this.pageIndex,
            withAllOrg: this.pEnableChildHierarchy ? 1 : 0,
            notGetStopUser: !this.includeStopUser,
            notGetCurrentUser: !this.pIncludeCurrentUser,
            orderField: this.orderField,
            userTypeMode: this.userTypeMode,
            filterId: this.filterId
        };
        this.stopNodeSelected = true;
        this.farrisTree.selectNode(this.treeListData[0].data.id);
        this.apiService.getPersonnelBySearch(this.absUserOrgUrl, this.absOrgType, this.searchText,
            requestParam).subscribe((res: any) => {
                const rcResData = res ? res.data : [];
                this.items = rcResData;
                this.pageTotalAll = res.totalCount;
                this.pageTotal = Math.ceil(this.pageTotalAll / this.pageSize);
                if (this.pageTotal === 0) {
                    this.pageTotal = 1;
                }
                this.unSelectedListLoad();
                this.stopNodeSelected = false;
            });
    }

    listenSearchText(e: KeyboardEvent) {
        if (e.code === 'Backspace' || e.code === 'Delete') {
            if (!this.searchText) {
                this.onSearching = false;
                this.switchPage({ pageIndex: 1 });
            }
        }
    }

    searchClear() {
        this.onSearching = false;
        this.switchPage({ pageIndex: 1 });
    }

    toggleIncludeCurrentUser(e) {
        this.includeCurrentUser = e;
    }

    /**
     * 切换包含子部门
     */
    toggleChildHierarchy(e) {
        this.enableChildHierarchy = e;
    }

    listenTreeExpand(obj: any) {
        // todo 分层加载
        if (this.enableHierarchicalLoading) {
            this.getOrganizationByParentNode(obj);
        }
    }

    getOrganizationByParentNode(parentNode: any) {
        const param: OrgRequestParam = {
            absOrgType: this.absOrgType,
            parentID: parentNode.id,
            notGetStopOrg: !this.includeStopOrg
        };
        this.apiService.getOrganization(this.absUserOrgUrl, param).subscribe((res: any) => {
            if (res && res.data) {
                const newDatas = [];
                res.data.forEach(item => {
                    if (this._isInArray(item[this.orgIdField], this.orgIdField, this.originalTreeData.data) === -1) {
                        item.selectable = true;
                        this.originalTreeData.data.push(item);
                        newDatas.push(item);
                    }
                });
                const nodes = this.dealData(newDatas, res.curAbsUser.defaultOrgPath);
                this.farrisTree.appendChildren(nodes, parentNode);
            }
        });
    }

    /**
         * 预处理组织数据
         */
    private dealData(data: any[], userPath: string) {
        const userPathList = [];
        if (!userPath) {
            return [];
        }
        let maxLength = userPath.length / 4;
        // todo 分层加载
        if (this.enableHierarchicalLoading) {
            maxLength = maxLength - 1;
        }
        for (let a = 0; a < maxLength; a++) {
            userPathList.push({ path: userPath.substring(0, 4 * (a + 1)) });
        }
        const newDatas = [];
        if (data.length) {
            data.forEach(item => {
                const newsDataObj: any = {
                    data: {
                        id: item.orgId,
                        name: item.name,
                        path: item.path,
                        layer: Number(item.layer)
                    },
                    expanded: true,
                    children: []
                };
                if (!!this.orgId && this.orgId !== '*') {
                    newsDataObj.selectable = item.selectable;
                }
                if (this._isInArray(item.path, 'path', userPathList) !== -1) {
                    newsDataObj.expanded = true;
                } else {
                    newsDataObj.expanded = false;
                    // todo 分层加载需要
                    if (this.enableHierarchicalLoading && item.isDetail === '0') {
                        newsDataObj.leaf = false;
                    }
                }
                newDatas.push(newsDataObj);
            });
        }
        return newDatas;
    }

    loadDataSelected(res) {
        this.pageIndex = 1;
        this.orgId = res.node.data.id;
        if (!this.stopNodeSelected) {
            this.onSearching = false;
            this.switchPage({ pageIndex: 1 });
        }
    }

    hideSearchOrgElement(e) {
        this.searchOrgElementVisible = false;
    }

    // 分层加载情况下，返回搜索后的组织列表
    private hlAssembleLocationOrgList() {
        if (!this.locationOrgText) {
            return;
        }
        const param: OrgRequestParam = {
            absOrgType: this.absOrgType,
            notGetStopOrg: !this.includeStopOrg
        };
        param.name = this.locationOrgText;
        param.getOrgAllPath = true;
        this.apiService.getOrganization(this.absUserOrgUrl, param).subscribe((res: any) => {
            this.searchOrgElementVisible = true;
            if (res && res.data) {
                this.locationOrgList = res.data;
                if (this.locationOrgList.length === 1) {
                    this.searchOrgElementVisible = false;
                    this.locationOrg(this.locationOrgList[0]);
                }
            }
        });
    }

    // 搜索定位组织
    searchLocationOrg(e: any) {
        if (!this.locationOrgText) {
            this.locationOrgList = [];
            this.searchOrgElementVisible = false;
            return;
        }
        if (e.code !== 'Enter' && e.type === 'keyup') {
            return;
        }
        // 分层加载
        if (this.enableHierarchicalLoading) {
            this.hlAssembleLocationOrgList();
        } else {
            this.locationOrgList = this.assembleLocationOrgList();
            this.searchOrgElementVisible = true;
            if (this.locationOrgList.length === 1) {
                this.searchOrgElementVisible = false;
                this.locationOrg(this.locationOrgList[0]);
            }
        }
    }

    // 定位到某个组织
    locationOrg(orgNode: any) {
        this.locationOrgList = [];
        this.searchOrgElementVisible = false;
        if (this.enableHierarchicalLoading) {
            // 分层加载
            const param: OrgRequestParam = {
                absOrgType: this.absOrgType,
                notGetStopOrg: !this.includeStopOrg,
                id: orgNode[this.orgIdField],
                getAllParentOrg: true
            };
            this.apiService.getOrganization(this.absUserOrgUrl, param).subscribe((res: any) => {
                if (!res || !res.data) {
                    return;
                }
                res.data.forEach(item => {
                    item.selectable = true;
                    if (this._isInArray(item[this.orgIdField], this.orgIdField, this.originalTreeData.data) === -1) {
                        this.originalTreeData.data.push(item);
                    }
                });
                this.treeListData = this.hlDataToTree(this.originalTreeData.data, 1, orgNode.path);
                const pathArr = this.getCurrentPath(orgNode);
                this.cd.detectChanges();
                this.expandTreeNode(this.treeListData, pathArr, Number(orgNode.layer));
                this.farrisTree.selectNode(orgNode[this.orgIdField]);
                this.orgId = orgNode[this.orgIdField];
                this.pageIndex = 1;
                this.switchPage({ pageIndex: 1 });
            });
        } else {
            const pathArr = this.getCurrentPath(orgNode);
            this.expandTreeNode(this.treeListData, pathArr, Number(orgNode.layer));
            this.farrisTree.selectNode(orgNode[this.orgIdField]);
            this.orgId = orgNode[this.orgIdField];
            this.pageIndex = 1;
            this.switchPage({ pageIndex: 1 });
        }
    }

    /**
     * 数据转换
     * @param parents 父级节点信息
     * @param treeSource 原始数据信息
     * @param layer 层级
     */
    treeData(parents: any, treeSource: any, layer: number) {
        parents.forEach(parent => {
            const parentPath = parent['data']['path'];
            const parentLayer = parent['data']['layer'];
            treeSource.forEach(item => {
                if (item && item['data'] && item['data']['path']) {
                    const itemPath = item['data']['path'];
                    const itemLayer = item['data']['layer'];
                    let targetPath: string;
                    if (itemPath && itemPath.length > parentPath.length) {
                        targetPath = itemPath.substr(0, Number(layer) * 4);
                    }
                    if (parentPath === targetPath && parentLayer === itemLayer - 1) {
                        parent['children'].push(item);
                    }
                    if (parentPath === targetPath) {
                        this.treeData([item], treeSource, Number(layer) + 1);
                    }
                }
            });

        })
    }

    private hlDataToTree(plainSource: any, layer: number, userPath: string) {
        const treeSource = this.hlDealData(plainSource, userPath);
        const parents = treeSource.filter(item => {
            return item.data.layer === layer;
        });
        this.treeData(parents, treeSource, 1);
        return parents;
    }

    // 返回已经该节点是否展开过
    private nodeExpanded(item: any, arr: TreeNode[], expanded: boolean[]): void {
        for (let i = 0; i < arr.length; i++) {
            if (item.data.id === arr[i].data.id) {
                expanded[0] = arr[i].expanded;
            } else if (arr[i].children.length) {
                this.nodeExpanded(item, arr[i].children, expanded);
            }
        }
    }

    private hlDealData(data: any[], userPath: string) {
        const userPathList = [];
        if (!userPath) {
            return [];
        }
        const maxLength = userPath.length / 4 - 1;
        for (let a = 0; a < maxLength; a++) {
            userPathList.push({ path: userPath.substring(0, 4 * (a + 1)) });
        }
        const newDatas = [];
        if (data.length) {
            data.forEach(item => {
                const newsDataObj: any = {
                    data: {
                        id: item.orgId,
                        name: item.name,
                        path: item.path,
                        layer: Number(item.layer)
                    },
                    expanded: false,
                    selectable: true,
                    children: []
                };
                let expanded = [];
                this.nodeExpanded(newsDataObj, this.treeListData, expanded);
                if (this._isInArray(item.path, 'path', userPathList) !== -1 || expanded[0]) {
                    newsDataObj.expanded = true;
                } else {
                    newsDataObj.expanded = false;
                    newsDataObj.leaf = false;
                }

                newDatas.push(newsDataObj);
            });
        }
        return newDatas;
    }

    // 展开节点
    private expandTreeNode(treeData: any, path: any[], layer: number) {
        treeData.forEach(node => {
            if (path.includes(node.data.path)) {
                this.farrisTree.expandNode(node.data.id);
            }
            if (node.data.layer < layer && node.children.length) {
                this.expandTreeNode(node.children, path, layer);
            }
        });
    }

    // 获取当前组织的path路径
    private getCurrentPath(orgNode: any) {
        const pathList = [];
        const length = orgNode.path.length / 4;
        for (let a = 0; a < length; a++) {
            pathList.push(orgNode.path.substring(0, 4 * (a + 1)));
        }
        return pathList;
    }

    // 全加载情况下，返回搜索后的组织列表
    private assembleLocationOrgList() {
        const originalArr = this.originalTreeData.data;
        const orgList = [];
        if (!this.locationOrgText) {
            return orgList;
        }
        const filterOrgs = originalArr.filter(item => item.name.indexOf(this.locationOrgText) !== -1);

        filterOrgs.forEach(org => {
            const parentNodes = [];
            this.getUnitParentsById(originalArr, org, parentNodes);
            org.orgAllPath = this.assembleOrgFullPath(parentNodes);
            org.orgAllPath = org.orgAllPath ? org.orgAllPath : org.name;
        });
        return filterOrgs;
    }

    /**
     * 根据节点信息获取到集团下单位的父级元素
     * @param array 原始数据
     * @param curNode 节点信息
     * @returns null
     */
    private getUnitParentsById(array: any[], curNode: any, resArray: any[]) {
        const parents = array.filter(item => {
            return Number(item.layer) === (Number(curNode.layer) - 1);
        });
        if (!parents) {
            return;
        }
        for (const item of parents) {
            if (item.orgId === curNode.parentId && curNode.valueType === 'depttype') {
                item.selectable = false;
                resArray.push(item);
                if (item.valueType === 'depttype') {
                    this.getUnitParentsById(array, item, resArray);
                }
                break;
            }
        }
    }

    private sortArray(arr: any[]) {
        if (arr.length === 0) {
            return arr;
        }
        arr = arr.sort((a: any, b: any) => {
            return Number(a.layer) - Number(b.layer);
        });
        return arr;
    }

    private assembleOrgFullPath(arr: any[]) {
        const sortedArr = this.sortArray(arr);
        let path = sortedArr.reduce((pre, cur, index) => {
            return pre + '_' + cur.name;
        }, '');
        path = path.substring(1);
        return path;
    }

    groupPersonnelDialogChange(e: any) {
        this.showGroupPersonnelDialog = e.showDialog;
        this.selectedGroupId = e.selectedGroupId;
    }

    beforePageIndexChange(inputEl: KeyboardEvent, maxNumber) {
        const target = inputEl.target as any;
        const currentPageNumber = Number(target.value);
        if (currentPageNumber > maxNumber) {
            target.value = maxNumber;
        } else {
            if (currentPageNumber < 1) {
                target.value = 1;
            }
        }
        this.pageIndex = Number(target.value);
    }

    toggleAll() {
        this.allChecked = !this.allChecked;
        this.items.forEach(item => {
            item.checked = this.allChecked;
            if (item.checked) {
                this._insertOneIntoSelections(item);
            } else {
                this.selections.forEach((selection, index) => {
                    if (item[this.idField] === selection[this.idField]) {
                        this.selections.splice(index, 1);
                    }
                });
            }
        });
    }

    toggleOne(item: any) {
        item.checked = !item.checked;
        if (item.checked) {
            this._insertOneIntoSelections(item);
            this.pageChangeCheck();
        } else {
            this.selections.forEach((selection, index) => {
                if (item[this.idField] === selection[this.idField]) {
                    this.selections.splice(index, 1);
                }
            });
            this.allChecked = false;
        }
    }

    turnOverPage(dir: string, num: number) {
        if ((dir === 'pre' && this.pageIndex === 1) || (dir === 'next' && this.pageIndex === this.pageTotal)) {
            return;
        }
        this.pageIndex = this.pageIndex + num;
        this.switchPage({ pageIndex: this.pageIndex });
    }

    // 人员展示翻页触发查询（！）
    switchPage(size) {
        this.pageIndex = size.pageIndex;
        if (this.onSearching) {
            this.onSearch(size);
            return;
        }
        this.assembleDataByOrgId();
    }

    toggleFavorites(e: any, item: any) {
        this.favoritesTool.toggleFavorites(e, item);
    }

    assembleDataByOrgId() {
        let orgIds = '';
        if (!this.orgId) {
            return;
        }
        orgIds = this.orgId === '*' ? '' : this.orgId;
        const requestParam: any = {
            withAllOrg: this.pEnableChildHierarchy ? 1 : 0,
            notGetStopUser: !this.includeStopUser,
            notGetCurrentUser: !this.pIncludeCurrentUser,
            orderField: this.orderField,
            userTypeMode: this.userTypeMode
        };
        if (this.enablePagination) {
            requestParam.pageSize = this.pageSize;
            requestParam.pageIndex = this.pageIndex;
        }
        if(this.withPermission) {
            requestParam.withPermission = true;
            requestParam.authOp = 'UserManage';
        }
        // 根据组织加载人员
        this.apiService.getPersonnelByOrgId(this.absUserOrgUrl, this.absOrgType, orgIds, requestParam).subscribe((res: any) => {
            const rcResData = res ? res.data : [];
            this.items = rcResData;
            this.pageTotalAll = res.totalCount;
            this.pageTotal = Math.ceil(this.pageTotalAll / this.pageSize);
            if (this.pageTotal === 0) {
                this.pageTotal = 1;
            }
            this.allChecked = false;
            this.unSelectedListLoad();
        });
    }

    /**
     * datagrid用于数据展示
     */
    unSelectedListLoad() {
        this.items.forEach(item => {
            item.inFavorites = false;
            item.checked = false;
            if (this._isInArray(item[this.idField], this.idField, this.favoritesList) !== -1) {
                item.inFavorites = true;
            }
            if (this._isInArray(item[this.idField], this.idField, this.selections) !== -1) {
                item.checked = true;
            }
            item.displayName = !!this.showUserCode ? ('[' + item.code + ']' + item.name) : item.name;
        });
        this.items = this.items.slice();
        this.pageChangeCheck();
        this.cd.detectChanges();
    }

    /**
     * 选中并插入当前行数据到右侧
     * @param data 当前行的数据
     * @param collection 暂存当前数据的集合
     */
    _insertOneIntoSelections(data: any) {
        // 如果当前数据已经移入右侧数据，则返回。
        if (this._isInArray(data[this.idField], this.idField, this.selections) > -1) {
            return;
        }
        this.selections.push(data);
        this.selections = this.selections.slice();
    }

    /**
     * 判断是否在内
     * @param value value
     * @param field field
     * @param array array
     * @returns 位置索引
     */
    _isInArray(value: string, field: string, array: any[]): number {
        if (!field || !value) {
            throw new Error('前2个参数必填');
        }
        return array.findIndex(item => value === item[field]);
    }

    /**
     * 翻页检查：人员展示区的勾选状态与已选人员区是否对应
     */
    pageChangeCheck() {
        const checkeds = this.items.filter(item => item.checked)
        if (this.items.length !== 0 && this.items.length === checkeds.length) {
            this.allChecked = true;
        }
    }
}
