import {
    Component, Input, EventEmitter, Output, OnInit, AfterViewInit, SimpleChanges, OnChanges, ChangeDetectorRef, Optional, Injector
} from '@angular/core';
import { ApiLocalService } from '../../utils/api-service';
import * as _ from 'lodash-es';
import { noDataImg } from '../../assets/resource';
import { forkJoin, of } from 'rxjs';
import { FavoritesTool } from '../../utils/favorites.tool';

@Component({
    selector: 'lib-candidate',
    templateUrl: './candidate.component.html',
    styleUrls: ['./candidate.component.css'],
})
export class CandidateComponent implements OnInit, AfterViewInit, OnChanges {

    @Input() idField: string;
    @Input() items = [];
    @Input() absUserOrgUrl: any;
    @Input() absOrgType: any;
    @Input() multiSelect: any;
    @Input() searchText: any;
    @Input() unSelectedList = [];
    @Input() userIds = '';
    @Input() unSelectedUserIds = '';
    @Input() type: any;
    @Input() includeStopUser: boolean;
    @Input() searchPage: boolean;
    @Input() includeCurrentUser: boolean;
    @Input() busNum: string;
    @Input() enableFavorites: boolean;
    @Input() validElementsInfo: string;
    @Output() selectionsChange = new EventEmitter<any>();
    @Output() candidateItemsChange = new EventEmitter<any[]>();

    private cdRef: ChangeDetectorRef
    allChecked = false;
    columns: any;
    selections: any[] = [];

    cloneUserIds = '';
    cloneSelections = [];
    itemsBackup = [];
    allIndexId = 'allIndexId';
    noDataImg = noDataImg;
    favoritesList = [];
    // 是否显示用户编号
    showUserCode = true;

    constructor(
        private apiService: ApiLocalService,
        private injector: Injector,
        @Optional() private favoritesTool: FavoritesTool
    ) {
        this.allIndexId = Math.random().toString(16).substring(3, 10);
        this.cdRef = this.injector.get(ChangeDetectorRef);
    }

    ngOnInit(): void {
        // 兼容老表单
        if (this.favoritesTool) {
            this.favoritesTool.favoritesSubject.subscribe(data => {
                this.favoritesList = data;
                if (this.enableFavorites) {
                    this.unSelectedListLoad();
                }
            });
        }
    }

    ngAfterViewInit(): void {
    }

    ngOnChanges(changes: SimpleChanges): void {

    }

    assembleData() {
        if (this.searchPage) {
            this.searchByCodeOrName();
        } else {
            this.assembleDataByUserId(this.unSelectedUserIds).subscribe(rcRes => {
                const rcResData = rcRes ? rcRes as any : [];
                this.unSelectedList = rcResData;
                this.unSelectedListLoad();
                this.candidateItemsChange.emit(this.unSelectedList);
            });
        }
    }

    toggleFavorites(e: any, item: any) {
        this.favoritesTool.toggleFavorites(e, item);
    }

    searchByCodeOrName() {
        this.items = this.items.filter(item => {
            return item.code.indexOf(this.searchText) !== -1 || item.name.indexOf(this.searchText) !== -1;
        });
    }

    restoreSelections() {
        this.selections = _.cloneDeep(this.cloneSelections);
        // this.emitSelections();
        this.toggleChecked();
        if (this.isAllChecked()) {
            this.allChecked = true;
        } else {
            this.allChecked = false;
        }
    }

    toggleAll() {
        if (!this.allChecked) {
            this.checkAll();
        } else {
            this.unCheckAll();
        }
        this.emitSelections();
    }

    toggleOne(data) {
        if (data.checked) {
            this.unSelectLine(data);
        } else {
            this.selectLine(data);
        }
        this.emitSelections();
    }
    /**
     * datagrid用于数据展示
     */
    unSelectedListLoad() {
        this.items = [];
        this.unSelectedList.forEach(res => {
            // const indexId = Math.random().toString(16).substring(2);
            // const item = {
            //     abbreviation: res.abbreviation,
            //     code: res.code,
            //     defaultOrgId: res.defaultOrgId,
            //     defaultOrgPath: res.defaultOrgPath,
            //     ext1: res.ext1,
            //     ext2: res.ext2,
            //     fullPathName: res.fullPathName,
            //     name: res.name,
            //     orgTypeId: res.orgTypeId,
            //     secLevel: res.secLevel,
            //     sortOrder: res.sortOrder,
            //     state: res.state,
            //     su: res.su,
            //     userId: res.userId,
            //     orgAllPath: res.orgAllPath,
            //     type: 'people',
            //     checked: false,
            //     inFavorites: false,
            //     indexId
            // };

            const item = _.cloneDeep(res);
            item.checked = false;
            item.inFavorites = false;
            item.indexId = Math.random().toString(16).substring(2);

            if (this._isInArray(res[this.idField], this.idField, this.selections) !== -1) {
                item.checked = true;
            }
            if (this._isInArray(res[this.idField], this.idField, this.favoritesList) !== -1) {
                item.inFavorites = true;
            }
            item.displayName = !!this.showUserCode ? ('[' + item.code + ']' + item.name) : item.name;
            this.items.push(item);
            this.itemsBackup.push(item);
        });
        const tempSelections = _.cloneDeep(this.selections);
        this.toggleChecked();
        this.selections = _.cloneDeep(tempSelections);
        this.cdRef.detectChanges();
    }

    /**
     * 全选后处理
     * @param dataArr 全选的值
     */
    checkAll() {
        this.items.forEach(item => {
            this._insertOneIntoSelections(item);
            item.checked = true;
        });
    }

    /**
     * 全部取消
     * @param dataArr 取消的数据
     * @returns void
     */
    unCheckAll() {
        this.items.forEach(item => {
            this.selections.forEach((selection, index) => {
                if (item[this.idField] === selection[this.idField]) {
                    this.selections.splice(index, 1);
                }
            });
            item.checked = false;
        });
    }

    /**
     * 当前行选中
     * @param data 当前行的数据（）
     */
    selectLine(data: any) {
        if (this.multiSelect) {
            this._insertOneIntoSelections(data);
        } else {
            this.userIds = '';
            this.selections = [];
            this.items.forEach(item => {
                if (item.checked) {
                    item.checked = false;
                }
            });
            this._insertOneIntoSelections(data);
        }
        if (this.isAllChecked()) {
            this.allChecked = true;
        }
    }

    /**
     * 单选取消选中
     * @param data 取消选中行
     */
    unSelectLine(data: any) {
        const index = this._isInArray(data[this.idField], this.idField, this.selections);
        if (index > -1) {
            this.selections.splice(index, 1);
        }
        if (this.allChecked) {
            this.allChecked = false;
        }
    }

    private emitSelections() {
        const emitObj = {
            selections: this.selections,
            token: 'candidate'
        };
        this.selectionsChange.emit(emitObj);
    }


    /**
     * todo
     */
    assembleDataByUserId(ids: string, initLoad = false) {
        if (!ids) {
            return of([]);
        }
        return this.apiService.getPersonnelByUserIds(this.absUserOrgUrl, this.absOrgType, ids);
    }

    /**
     *  取消选中
     * @param $event 取消勾选的数据
     */
    cancelSelections($event) {
        if (this.items.findIndex(item => item[this.idField] === $event.id) === -1) {
            return;
        }
        this.items.forEach(item => {
            if (item[this.idField] === $event.id) {
                item.checked = false;
            }
        });
        this.selections.forEach((selection, index) => {
            if ($event.id === selection[this.idField]) {
                this.selections.splice(index, 1);
            }
        });
        this.allChecked = false;
    }

    /**
     * 全部取消勾选
     */
    clearCheckeds() {
        this.items.forEach(item => {
            item.checked = false;
        });
        this.allChecked = false;
        this.selections = [];
    }

    clearUserIds() {
        this.userIds = '';
    }
    /**
     * 选中并插入当前行数据到右侧
     * @param data 当前行的数据
     * @param collection 暂存当前数据的集合
     */
    _insertOneIntoSelections(data: any) {
        // 如果当前数据已经移入右侧数据，则返回。
        if (this._isInArray(data[this.idField], this.idField, this.selections) > -1) {
            return;
        }
        this.selections.push(data);
        this.selections = this.selections.slice();
    }

    /**
     * 判断是否在内
     * @param value value
     * @param field field
     * @param array array
     * @returns 位置索引
     */
    _isInArray(value: string, field: string, array: any[]): number {
        if (!field || !value) {
            throw new Error(this.validElementsInfo);
        }
        return array.findIndex(item => value === item[field]);
    }

    /**
     * 翻页检查：人员展示区的勾选状态与已选人员区是否对应
     */
    toggleChecked() {
        this.items.forEach(item => {
            if (this._isInArray(item[this.idField], this.idField, this.selections) > -1) {
                item.checked = true;
            } else {
                item.checked = false;
            }
        });
        if (this.isAllChecked()) {
            this.allChecked = true;
        } else {
            this.allChecked = false;
        }
    }

    private isAllChecked(): boolean {
        let count = 0;
        let allChecked = false;

        this.items.forEach(item => {
            this.selections.forEach(selection => {
                if (item[this.idField] === selection[this.idField]) {
                    count += 1;
                }
            });
        });
        if (this.items.length === count && count > 0) {
            allChecked = true;
        }
        return allChecked;
    }

    trackByFn(index, item) {
        return index;
    }

}
